<template>

<img class="poster" :src="data.imageURL" alt="" :class="{animate: isAnimation}">
<img :src="next.imageURL" alt="" class="poster next" :class="{animate: isAnimation}">

<div class="title-section" :class="{animate: isAnimation}">
