  <h2>{{data.job}}</h2>
  <h3>{{data.year}} | {{data.rating}} &#9734;</h3>
</div>

<div class="title-section next" :class="{animate: isAnimation}">
  <h1>{{next.title}}</h1>
  <h2>{{next.job}}</h2>
  <h3>{{next.year}} | {{next.rating}} &#9734;</h3>
</div>

