<script>

export default {
  name: 'App',
  data(){
    return {
      data: {},
      next: {},
      isAnimation: false
    }
  },
  async beforeCreate()
  {
    await fetch("https://api.loganreuter.com/dad/update", {method: "POST"})
  },
  async created(){
    await fetch("https://api.loganreuter.com/dad/")
    .then((res) => {return res.json()})
    .then((data) => this.data = data)

    await fetch("https://api.loganreuter.com/dad/next")
      .then((res) => {return res.json()})
      .then((data) => this.next = data)
    
    setInterval(() => {
      this.data = this.next;
      this.isAnimation = true
      }, 55000)
    
    setInterval(async () => {
      this.isAnimation = false
      await fetch("https://api.loganreuter.com/dad/next")
      .then((res) => {return res.json()})
      .then((data) => {
        this.next = data
      })
    }, 60000)
  },
  methods: {
    async updateData() {
      fetch
    }
  }
}
</script>

